.App {
  height: 100%;
  max-width: 800px;
  min-width: 300px;
  margin: 0 auto;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.App-contents {
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  /* background-color: red; */
}

.About, .Contact {
  width: 100%;
  padding: 16px;
  margin: auto;
  /* background-color: red; */
}

.About-pic {
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
}

.About-title, .About-desc {
  font-family: Roboto, sans-serif;
}

.About-title {
  font-weight: 400;
}

.About-desc {
  font-weight: 400;
  line-height: 1.5em;
  color: #000a;
}


